<template>
  <el-form
    class="rocket-form"
    :class="{ dialog: type === 'dialog' }"
    ref="searchForm"
    :model="value"
    :inline="inline"
    @submit.native.prevent="handleQuery"
  >
    <!-- -->
    <template v-if="inline">
      <template v-for="(item, index) in form">
        <FormItem
          :key="index"
          :item="item"
          v-bind="item"
          :value="value[item.model]"
          @input="(val) => handleInput(item, val)"
        />
      </template>
      <el-form-item>
        <el-button @click="handleReset"></el-button>
        <el-button type="primary" @click="handleQuery"></el-button>
      </el-form-item>
    </template>
    <!-- -->
    <el-row type="flex" :gutter="20" v-else>
      <template v-for="(item, index) in form">
        <el-col
          :key="index"
          :xs="24"
          :sm="24"
          :md="md"
          :lg="lg"
          :xl="xl"
          :class="{ hidden: index >= hiddenIndex && !isOpen }"
        >
          <FormItem
            :key="index"
            :item="item"
            v-bind="item"
            :value="value[item.model]"
            @input="(val) => handleInput(item, val)"
          />
        </el-col>
      </template>
      <el-col
        :xs="24"
        :sm="24"
        :md="md"
        :lg="lg"
        :xl="xl"
        :offset="offset"
        class="text-right mb20"
      >
        <el-button @click="handleReset"></el-button>
        <el-button type="primary" @click="handleQuery"></el-button>
        <el-button type="text" v-if="showOpen" @click="handleOpen" size="medium"
          >{{ isOpen ? "" : ""
          }}<i :class="[isOpen ? 'el-icon-arrow-up' : 'el-icon-arrow-down']"></i
        ></el-button>
      </el-col>
    </el-row>
  </el-form>
</template>
<script>
import FormItem from './../components/FormItem'
/**
 * 
 * 
 * 24
 * @param {formLength} JSON
 * @param {isOpen} 
 * @param {mode} 
 */
const getResult = (formLength, isOpen, mode) => {
  const w = document.documentElement.clientWidth
  let len = 0
  let hiddenIndex = 0
  let offset = 0
  let showOpen = true
  let md = 12 // 1200px2
  let lg = 8 // 1200px3
  let xl = 6 // 1920px4
  if (mode === 'dialog') {
    if (isOpen) {
      hiddenIndex = 0
      offset = formLength % 2 === 0 ? 12 : 0
    } else {
      hiddenIndex = 1
      offset = formLength > 1 ? 0 : 12
    }
    md = 12
    lg = 12
    xl = 12
  } else {
    if (w >= 1920) {
      hiddenIndex = 3
      len = formLength % 4
      if (isOpen) {
        if (len === 0) offset = 18
        else if (len === 1) offset = 12
        else if (len === 2) offset = 6
        else if (len === 3) offset = 0
      } else if (formLength > 3) {
        offset = 0
      } else {
        if (len === 0) offset = 0
        else if (len === 1) offset = 12
        else if (len === 2) offset = 6
        else if (len === 3) offset = 0
      }
    } else if (w >= 1200) {
      // 
      hiddenIndex = 2
      len = formLength % 3
      if (isOpen) {
        if (len === 0) offset = 16
        else if (len === 1) offset = 8
        else if (len === 2) offset = 0
      } else if (formLength > 2) {
        offset = 0
      } else {
        if (len === 0) offset = 0
        else if (len === 1) offset = 8
        else if (len === 2) offset = 0
      }
    } else if (w >= 992) {
      hiddenIndex = 1
      if (isOpen) {
        if (formLength % 2 === 0) offset = 12
      }
    } else {
      hiddenIndex = 1
    }
    if (formLength <= hiddenIndex) {
      showOpen = false
    }
  }

  return {
    hiddenIndex,
    offset,
    showOpen,
    md,
    lg,
    xl,
    w,
  }
}
// resize
function debounce (method, delay, immediate) {
  let timer = null
  if (immediate) {
    method && method()
  }
  return function () {
    const self = this
    const args = arguments
    timer && clearTimeout(timer)
    timer = setTimeout(function () {
      method.apply(self, args)
    }, delay)
  }
}
export default {
  name: 'QueryForm',
  props: {
    inline: Boolean, // truefalse
    type: String, // dialogQueryFormdialogQueryForm
    form: Array, // JSON
    model: Object, // v-model
  },
  components: { FormItem },
  data () {
    return {
      value: { ...this.model }, // 
      md: 12, // 992-1200 , 
      lg: 8, // 1200 - 1920 , 3
      xl: 6, // >=1920 , 
      showOpen: true, // /
      isOpen: false, // 
      hiddenIndex: 0, // 
      offset: 0, // 
      screenWidth: 0, // 
    }
  },
  mounted () {
    window.onresize = debounce(this.handleLayout, 500, true)
  },
  methods: {
    /**
     * 
     * @callback(val,values,model)//model
     */
    handleInput (item, val) {
      const { action } = item
      /**
       * type: 'reset' 
       * model: 'all' 
       * model: ['state'] 
       * change
       */
      if (action && action.type === 'reset') {
        const modelList = action.model
        // 
        if (modelList === 'all') {
          this.handleReset()
          this.value[item.model] = val
        } else if (modelList) {
          // 
          modelList.map(key => {
            if (Array.isArray(this.value[key])) {
              this.value[key] = []
            } else {
              this.value[key] = undefined
            }
            return key
          })
        }
      }
      if (typeof item.change === 'function') {
        item.change(val, this.value, item.model)
      }
      this.value = { ...this.value, [item.model]: val }
    },
    /**
     * 
     * $refsAPI
     */
    handleReset () {
      this.$refs.searchForm.resetFields()
      this.$emit('update:model', { ...this.value })
      this.$emit('handleReset', 1)
    },
    /**
     * 
     */
    handleQuery () {
      this.$emit('update:model', { ...this.value })
      this.$emit('handleQuery', 1)
    },
    /**
     * /
     */
    handleOpen () {
      this.isOpen = !this.isOpen
      const res = getResult(this.form.length, this.isOpen, this.mode)
      this.hiddenIndex = res.hiddenIndex
      this.offset = res.offset
      this.md = res.md
      this.lg = res.lg
      this.xl = res.xl
    },
    /**
     * resize
     */
    handleLayout () {
      // 
      const { hiddenIndex, offset, showOpen, md, lg, xl, w } = getResult(
        this.form.length,
        this.isOpen,
        this.mode
      )
      this.hiddenIndex = hiddenIndex
      this.offset = offset
      this.showOpen = showOpen
      this.md = md
      this.lg = lg
      this.xl = xl
      this.screenWidth = w
    },
  },
  /**
   * 
   */
  unmounted () {
    window.onresize = null
  },
  /**
   * model
   */
  watch: {
    model: function (val) {
      this.value = { ...val }
    }
  }
}
</script>
<style lang="scss" scoped>
// 
.rocket-form {
  &.dialog {
    background-color: #f7f8fa;
    border: none;
  }
  .hidden {
    display: none;
  }
  .el-row--flex {
    flex-wrap: wrap;
    .el-form-item {
      display: flex;
      .el-form-item__content {
        flex: 1;
        .el-date-editor {
          width: 100%;
        }
      }
    }
  }
}
</style>
